import bannerVideo from './bannerVideo'

export default {
  layout: 'banner',
  components: {
    bannerVideo
  },
  data: () => ({
    isMobile: null,
  }),
  mounted() {
    this.isMobile = window.innerWidth < 1000

    window.addEventListener('resize', () => {
      this.isMobile = window.innerWidth < 1000
    })
  },
}
